import React from 'react';
import { observer } from 'mobx-react';
import './Aptitudes.css'
import STORE from '../../../stores/Store';

class Aptitudes extends React.Component {
    constructor(props){
        super(props);
        this.interval = this.interval.bind(this);
        let time = setInterval(this.interval, 20000);

        this.state = {
            count: 0,
            timeout: time
        }

    }

    interval () {
        this.setState((prev)=>{return {count : prev.count < STORE.aptitudes.length - 1? (prev.count + 1) : 0 } });
    }

    render(){
        return (
            <section className='Aptitudes screen-size'>
                <h4>{STORE.aptitudes[this.state.count].title}</h4>
                <p>{STORE.aptitudes[this.state.count].description}</p>
    
                <div className='row tick-marks'>
                    {STORE.aptitudes.map((elem, index) => {
                        return <span className={index === this.state.count ? 'selected' : ''} key={index}
                            onClick={() => {
                                clearInterval(this.state.timeout);
                                let temp = setInterval(this.interval, 20000);
                                this.setState({count: index, timeout: temp});
                            }}></span>
                    })}
                </div>
    
            </section>
        )
    }
}

export default observer(Aptitudes);